/**
 * API client for backend communication
 * All data comes from FastAPI backend
 */

const API_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:8000';

export interface Player {
  id: string;
  name: string;
  rating: number;
  initial_rating: number;
  peak_rating: number;
  is_cms: boolean;
  created_at: string;
  updated_at: string;
  matches_count: number;
}

export interface Match {
  id: string;
  player1_id: string;
  player2_id: string;
  winner_id: string;
  player1_score: number;
  player2_score: number;
  max_score: number;
  player1_rating_before: number;
  player2_rating_before: number;
  player1_rating_change: number;
  player2_rating_change: number;
  date: string;
  created_at: string;
}

/**
 * Fetch all players from backend
 */
export async function fetchPlayers(): Promise<Player[]> {
  const response = await fetch(`${API_URL}/api/players`);
  if (!response.ok) {
    throw new Error(`Failed to fetch players: ${response.statusText}`);
  }
  return response.json();
}

/**
 * Fetch specific player by ID
 */
export async function fetchPlayer(id: string): Promise<Player> {
  const response = await fetch(`${API_URL}/api/players/${id}`);
  if (!response.ok) {
    throw new Error(`Failed to fetch player: ${response.statusText}`);
  }
  return response.json();
}

/**
 * Fetch all matches (optionally filtered by player)
 */
export async function fetchMatches(playerId?: string): Promise<Match[]> {
  const url = playerId 
    ? `${API_URL}/api/matches?player_id=${playerId}`
    : `${API_URL}/api/matches`;
  
  const response = await fetch(url);
  if (!response.ok) {
    throw new Error(`Failed to fetch matches: ${response.statusText}`);
  }
  return response.json();
}
